.field {
    position: relative;
    width: 100%;
    padding: 0;
    border: none;

    label {
        display: flex;
        flex-direction: column;
        gap: 8px;
        width: 100%;
        padding: 0;
        border: none;

        @include max(tablet) {
            gap: 4px;
        }
    }

    &--invalid {
        .field__error {
            display: block;
        }
    }

    &__title {
        font-weight: 400;
        font-size: 14px;
        line-height: 1em;
    }

    &__input,
    &__textarea {
        width: 100%;
        padding: 22.5px 20px;
        font-family: $ff-inter;
        font-size: 16px;
        line-height: 1.1875em;
        font-weight: 600;
        outline: none;
        color: $c-text;
        background-color: $c-bg-input;
        border-radius: $br-btn-input;

        @include max(mobile) {
            padding: 13px 20px;
            font-size: 16px;
            border-radius: 10px;
            min-height: 45px;
        }

        &::placeholder {
            color: $c-text-breadcrumbs;
            opacity: 1;
        }
    }

    &--invalid &__input,
    &--invalid &__textarea,
    &--invalid .select2 {
        border-color: $c-error !important;
    }

    &__textarea {
        resize: none;
    }

    &__select.select2-hidden-accessible {
        opacity: 0;
        visibility: hidden;
    }

    &__error {
        position: absolute;
        top: calc(100% - 1px);
        left: 0;
        width: 100%;
        font-size: 12px;
        line-height: 1.4em;
        color: $c-error;
        display: none;
    }

    &.disabled {
        opacity: 0.5;
        pointer-events: none;
    }

