.text-editor *:first-child {
  margin-top: 0 !important;
}

.text-editor *:last-child {
  margin-bottom: 0 !important;
}

.text-editor h2,
.text-editor h3 {
  font-family: "Rooftop", Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 22px;
  line-height: 1.3em;
  letter-spacing: 0;
  margin-bottom: 0.9em;
}

.text-editor p,
.text-editor ul,
.text-editor ol,
.text-editor a,
.text-editor li {
  font-size: inherit;
}

.text-editor p,
.text-editor ul,
.text-editor ol {
  margin-bottom: 1.25em;
}

.text-editor ol,
.text-editor ul {
  display: flex;
  flex-direction: column;
  gap: 0.9375em;
  list-style-type: none;
}

.text-editor ol {
  counter-reset: num;
}

.text-editor ol li {
  position: relative;
  padding-left: 1.5em;
  line-height: 1.4em;
}

.text-editor ol li:before {
  content: counter(num) ".";
  counter-increment: num;
  position: absolute;
  left: auto;
  right: calc(100% - 1em);
  font-weight: 500;
}

.text-editor ul li {
  position: relative;
  padding-left: 2.5625em;
  line-height: 1.4375em;
}

.text-editor ul li:before {
  content: "";
  position: absolute;
  left: 1.25em;
  top: 0.71875em;
  width: 0.5em;
  height: 0.5em;
  transform: translateY(-50%);
  border-radius: 100%;
  background-color: #FC00FF;
}

.text-editor a {
  color: var(--c-accent);
  transition: all 0.3s linear;
}

.text-editor a:hover {
  color: #2427e1;
}

.text-editor img,
.text-editor video,
.text-editor iframe,
.text-editor .video {
  max-width: 100%;
  margin: 1.875em auto;
  border-radius: 24px;
}

.text-editor img[align=left] {
  width: 100%;
  object-fit: contain;
  object-position: left;
}

.text-editor img[align=right] {
  width: 100%;
  object-fit: contain;
  object-position: right;
}

