.modal.hystmodal .hystmodal__window {
  transition: all 0.3s linear;
  transform: scale(0);
  opacity: 0;
  width: auto;
  background-color: transparent;
}

.modal.hystmodal--active .hystmodal__window {
  margin: 0;
  transition: all 0.3s linear;
  transform: scale(1);
  opacity: 1;
}

.hystmodal__shadow.hystmodal__shadow--show {
  background-color: var(--c-text);
  opacity: 0.3;
}

.modal {
  box-sizing: border-box;
}

.modal__close {
  position: absolute;
  right: 16px;
  top: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: auto;
  min-width: auto;
  cursor: pointer;
  z-index: 20;
  color: #9299A7;
  transition: color 0.3s;
  width: 48px;
  height: 48px;
  border: 1px solid #E1E6EC;
  border-radius: 4px;
}

.modal__close:hover {
  color: #2427e1;
}

.modal__close svg {
  width: 24px;
  height: 24px;
}

.modal__title {
  letter-spacing: -0.02em;
  text-align: center;
  padding: 0 40px;
  color: var(--c-text);
}

.modal__content {
  position: relative;
  background: #fff;
  width: 100%;
  max-width: 540px;
  min-width: 540px;
  z-index: 9999;
  padding: 20px 16px;
  border-radius: 4px;
  max-height: 95vh;
  overflow-y: auto;
}

.modal__inner {
  margin-top: 24px;
}

