.field--checkbox {
  width: fit-content;
}

.field--checkbox label {
  cursor: pointer;
  position: relative;
  flex-direction: row-reverse;
  align-items: center;
  width: fit-content;
}

.field--checkbox input {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  width: 0;
  height: 0;
  visibility: hidden;
}

.field--checkbox .fake-checkbox {
  display: block;
  position: relative;
  width: 40px;
  height: 26px;
  background-color: #B3B7C0;
  flex-shrink: 0;
  flex-grow: 0;
  transition: all 0.3s linear;
  border-radius: 13px;
}

.field--checkbox .fake-checkbox::after {
  content: "";
  position: absolute;
  width: 22px;
  height: 22px;
  left: 2px;
  top: 50%;
  transform: translateY(-50%);
  background-color: #fff;
  border-radius: 100%;
  transition: all 0.3s linear;
}

.field--checkbox input:checked + .fake-checkbox {
  background-color: var(--c-accent);
}

.field--checkbox input:checked + .fake-checkbox::after {
  left: 16px;
}

.field--tab {
  width: fit-content;
}

.field--tab label {
  cursor: pointer;
  position: relative;
  width: max-content;
  cursor: pointer;
}

.field--tab input {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  width: 0;
  height: 0;
  visibility: hidden;
}

.field--tab .fake-checkbox {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 30px;
  gap: 10px;
  background-color: #EDF0F6;
  transition: all 0.3s linear;
  border-radius: 16px;
  border: 1px solid var(--c-accent);
  color: var(--c-accent);
  font-weight: 600;
  font-size: 16px;
  line-height: 1.1875em;
  letter-spacing: 0;
  min-height: 64px;
}

.field--tab .fake-checkbox svg {
  display: none;
  width: 24px;
  height: 24px;
}

.field--tab input:checked + .fake-checkbox,
.field--tab label:hover .fake-checkbox {
  background-color: var(--c-accent);
  color: #fff;
}

.field--tab input:checked + .fake-checkbox svg {
  display: block;
}

.field--double {
  display: flex;
  align-items: stretch;
  position: relative;
}

.field--double::after {
  content: "";
  background-color: #939AA7;
  width: 1px;
  height: 62.5%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.field--double .field__input {
  flex: 0 0 50%;
  padding: 22.5px 16px 22.5px 20px;
  border-radius: 14px 0 0 14px;
}

.field--double .field__input--second {
  padding: 22.5px 20px 22.5px 16px;
  border-radius: 0 14px 14px 0;
}

.field--double .select2.select2-container--default {
  border-radius: 14px 0 0 14px;
}

.field--double .field__select--second + .select2.select2-container--default {
  border-radius: 0 14px 14px 0;
}

.field--double .select2.select2-container--default .select2-selection--single,
.field--double .select2.select2-container--default .select2-selection--multiple {
  padding: 22px 16px 22px 20px;
}

.select2 {
  display: flex;
  align-items: center;
  width: 100% !important;
  border-radius: 14px;
  background-color: #EDF0F6;
}

.select2 .selection {
  width: 100%;
}

.select2 .select2-selection__clear {
  display: none;
}

.select2.select2-container--default .select2-search--inline .select2-search__field {
  margin-top: 0;
}

.select2.select2-container--default .select2-selection--single,
.select2.select2-container--default .select2-selection--multiple {
  padding: 22px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--c-text);
  border-radius: 0;
  width: 100%;
  height: auto;
  border: none;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s;
  min-height: 64px;
}

.select2.select2-container--default .select2-selection--single .select2-selection__rendered,
.select2.select2-container--default .select2-selection--single .select2-selection__placeholder,
.select2.select2-container--default .select2-selection--multiple .select2-selection__rendered,
.select2.select2-container--default .select2-selection--multiple .select2-selection__placeholder {
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.1875em;
  letter-spacing: 0;
  color: #878E9C;
  width: 100%;
  padding: 0;
}

.select2.select2-container--default .select2-selection--single .select2-selection__rendered,
.select2.select2-container--default .select2-selection--multiple .select2-selection__rendered {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  color: var(--c-text);
}

.select2.select2-container--default .select2-selection--single .select2-selection__rendered .select2-selection__choice,
.select2.select2-container--default .select2-selection--multiple .select2-selection__rendered .select2-selection__choice {
  margin-top: 0;
  margin-right: 0;
  background-color: transparent;
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  height: 19px;
  font-size: 13px;
  color: var(--c-accent);
  border-color: var(--c-accent);
}

.select2.select2-container--default .select2-selection--single .select2-selection__rendered .select2-selection__choice .select2-selection__choice__remove,
.select2.select2-container--default .select2-selection--multiple .select2-selection__rendered .select2-selection__choice .select2-selection__choice__remove {
  margin-right: 0;
  margin-left: 5px;
  font-size: 20px;
  color: var(--c-accent);
}

.select2.select2-container--default .select2-selection--single .select2-selection__arrow,
.select2.select2-container--default .select2-selection--multiple .select2-selection__arrow {
  position: relative;
  flex-shrink: 0;
  flex-grow: 0;
  height: 10px;
  width: 10px;
  display: block;
}

.select2.select2-container--default .select2-selection--single .select2-selection__arrow b,
.select2.select2-container--default .select2-selection--multiple .select2-selection__arrow b {
  display: none;
}

.select2.select2-container--default .select2-selection--single .select2-selection__arrow::before,
.select2.select2-container--default .select2-selection--multiple .select2-selection__arrow::before {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  background: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.4' clip-path='url(%23clip0_636_25816)'%3E%3Cpath d='M5.20799 7.76458C5.03623 7.76458 4.86449 7.69821 4.73353 7.56573L0.612623 3.39498C0.35048 3.12967 0.35048 2.69951 0.612623 2.43431C0.874661 2.1691 1.2996 2.1691 1.56176 2.43431L5.20799 6.12482L8.85425 2.43444C9.11639 2.16923 9.54129 2.16923 9.8033 2.43444C10.0656 2.69964 10.0656 3.1298 9.8033 3.39511L5.68246 7.56586C5.55144 7.69836 5.3797 7.76458 5.20799 7.76458Z' fill='%230F0908'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_636_25816'%3E%3Crect width='9.58401' height='9.69985' fill='white' transform='translate(0.416016 0.150078)'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A");
  background-size: 100%;
  transition: all 0.3s;
  transform: rotate(0deg);
}

.select2.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow::before,
.select2.select2-container--default.select2-container--open .select2-selection--multiple .select2-selection__arrow::before {
  content: "";
  transform: rotate(180deg);
  transition: all 0.3s;
}

.select2-container.select2-container--default.select2-container--open {
  z-index: 40;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown {
  background-color: #fff;
  border: 1px solid #E1E6EC;
  border-radius: 14px;
  padding: 10px 5px 10px 10px;
  top: 10px;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-search {
  display: none;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__options {
  display: flex;
  flex-direction: column;
  gap: 7px;
  max-height: 223px;
  padding-left: 0;
  padding-right: 5px;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__options::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__options::-webkit-scrollbar-track {
  background-color: #878E9C;
  border-radius: 6px;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__options::-webkit-scrollbar-thumb {
  background-color: #EDF0F6;
  border-radius: 6px;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__option {
  color: #878E9C;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 1.2em;
  letter-spacing: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  background: transparent;
  padding: 6px 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s linear;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__option:hover {
  transition: all 0.3s linear;
  background-color: #F4F6FA;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__option.select2-results__option--highlighted {
  background-color: #F4F6FA;
  transition: all 0.3s linear;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: var(--c-text);
  transition: color 0.3s linear;
}

.field.active .select2 {
  background-color: var(--c-text);
  border-color: #E1E6EC;
  color: #fff;
}

.field.active .select2 .select2-selection--single .select2-selection__rendered,
.field.active .select2 .select2-selection--multiple .select2-selection__rendered {
  color: #fff;
}

.field.active .select2 .select2-selection--single .select2-selection__arrow::before,
.field.active .select2 .select2-selection--multiple .select2-selection__arrow::before {
  background: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.332 6.00004L7.9987 9.33337L4.66537 6.00004' stroke='%23fff' stroke-linecap='square'/%3E%3C/svg%3E") 0 0/100% 100% no-repeat;
}

.field--white .field__input,
.field--white .field__textarea,
.field--white .select2 {
  background-color: #fff;
}

