.field-range {
  position: relative;
  padding-bottom: 60px;
  width: calc(100% - 25px);
  margin-left: auto;
  margin-right: auto;
}

.field-range--margins {
  margin-top: 50px;
  margin-bottom: 50px;
}

.field-range__input {
  opacity: 0;
  width: 0;
  height: 0;
  position: absolute;
  left: 0;
  top: 0;
  overflow: hidden;
}

.field-range__title {
  margin-bottom: 5px;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.1875em;
  letter-spacing: 0;
  color: #878E9C;
}

.field-range__values {
  display: none;
}

.field-range__value {
  flex: 0 1 auto;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 0;
  color: #878E9C;
}

.field-range .noUi-horizontal {
  height: 10px;
}

.field-range .noUi-target {
  position: absolute;
  top: 0;
  left: 0;
  height: 10px;
  width: 100%;
  border: none;
  background-color: #EDF0F6;
  box-shadow: none;
  border-radius: 12px;
}

.field-range__title + .noUi-target {
  top: auto;
  bottom: 50px;
}

.field-range .noUi-connect {
  background-color: var(--c-accent);
  border-radius: 12px;
}

.field-range .noUi-handle {
  width: 25px;
  height: 25px;
  right: -12.5px;
  top: -7.5px;
  border: 5px solid var(--c-accent);
  background-color: #EDF0F6;
  box-shadow: none;
  border-radius: 100%;
  cursor: pointer;
}

.field-range .noUi-handle:before,
.field-range .noUi-handle::after {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='19' height='14' viewBox='0 0 19 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.49519 2.125C7.64989 0.125003 10.5366 0.125 11.6913 2.125L18.1865 13.375H0L6.49519 2.125Z' fill='%23EDF0F6'/%3E%3C/svg%3E");
  background-size: 100%;
  position: absolute;
  background-color: transparent;
  width: 19px;
  height: 16px;
  top: 21px;
  left: 50%;
  transform: translateX(-50%);
}

.field-range .noUi-tooltip {
  bottom: auto !important;
  top: calc(100% + 31px);
  background-color: #EDF0F6;
  border-radius: 12px;
  border: none;
  padding: 10px;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1em;
  letter-spacing: 0;
}

.field-range__title + .noUi-target .noUi-tooltip {
  top: calc(100% + 19px);
}

.field-range__title + .noUi-target + .field-range__values {
  margin-top: 58px;
}

