.btn svg,
.btn img {
  width: 16px;
  height: 16px;
}

.btn img {
  object-fit: contain;
}

.btn--accent {
  color: #fff;
  background-color: var(--c-accent);
  border: 1px solid var(--c-accent);
  border-color: var(--c-accent);
  transition: all 0.3s linear;
}

.btn--accent:hover {
  background-color: #2427e1;
  border-color: #2427e1;
}

.btn--light-accent {
  color: var(--c-accent);
  background-color: #EDF0F6;
  border: 1px solid var(--c-accent);
  transition: all 0.3s;
  border-radius: 14px;
}

.btn--light-accent svg {
  color: var(--c-accent);
  transition: all 0.3s;
}

.btn--light-accent:hover {
  border-color: #2427e1;
  color: #2427e1;
  background-color: #EAF0FC;
}

.btn--light-accent:hover svg {
  color: #2427e1;
}

.btn--bordered {
  color: var(--c-accent);
  background-color: transparent;
  border: 1px solid var(--c-accent);
  transition: all 0.3s;
  border-radius: 14px;
}

.btn--bordered svg {
  color: var(--c-accent);
  transition: all 0.3s;
}

.btn--bordered:hover {
  border-color: #2427e1;
  color: #2427e1;
}

.btn--bordered:hover svg {
  color: #2427e1;
}

.btn--grey {
  color: #fff;
  background-color: #202020;
  transition: all 0.3s;
}

.btn--grey:hover {
  background-color: #2427e1;
  color: #fff;
}

.btn--noborder {
  border: none;
  transition: color 0.3s linear;
}

.btn--noborder svg {
  color: #4e4e4e;
  transition: color 0.3s linear;
  margin-bottom: 3px;
}

.btn--noborder:hover {
  color: #2427e1;
}

.btn--noborder:hover svg {
  color: #2427e1;
}

.btn--link {
  width: fit-content;
  padding: 0;
  color: var(--c-text);
  font-weight: 400;
  font-size: 13px;
  line-height: 1em;
  gap: 10px;
}

.btn--link .btn__text {
  white-space: nowrap;
}

.btn--link svg {
  width: 20px;
  height: 20px;
}

.btn--share {
  border: none;
  transition: color 0.3s linear;
  color: var(--c-accent);
  font-size: 16px;
}

.btn--share svg {
  color: var(--c-accent);
  transition: color 0.3s linear;
  width: 24px;
  height: 24px;
}

.btn--share:hover {
  color: #2427e1;
}

.btn--share:hover svg {
  color: #2427e1;
}

.btn--238 {
  width: fit-content;
  min-width: 238px;
  padding-left: 20px;
  padding-right: 20px;
}

.btn--155 {
  width: fit-content;
  min-width: 155px;
  padding-left: 20px;
  padding-right: 20px;
}

.btn--165 {
  width: fit-content;
  min-width: 165px;
  padding-left: 20px;
  padding-right: 20px;
}

.btn--full {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
}

.btn--content {
  width: fit-content;
}

.btn--narrow {
  width: fit-content;
  padding-left: 20px;
  padding-right: 20px;
}

.btn--small {
  padding: 10px 15px 10px 10px;
  font-size: 12px;
  gap: 5px;
  border-radius: 14px;
}

.btn--small svg {
  width: 15px;
  height: 15px;
}

.btn--footer {
  width: fit-content;
  padding: 12px 23px;
  text-transform: uppercase;
}

.btn--header {
  width: fit-content;
  padding: 9.5px;
  white-space: nowrap;
  border-radius: 24px;
}

.btn--header svg {
  width: 28px;
  height: 28px;
  display: none;
}

.btn--toggle .show {
  display: block;
}

.btn--toggle .hide {
  display: none;
}

.btn--toggle.expanded .show {
  display: none;
}

.btn--toggle.expanded .hide {
  display: block;
}

.breadcrumbs {
  position: relative;
  z-index: 3;
  overflow: hidden;
}

.breadcrumbs__list {
  list-style-type: none;
  display: flex;
  gap: 2px;
  font-size: 12px;
  color: var(--c-text);
  overflow-x: auto;
  overflow-y: hidden;
}

.breadcrumbs__item {
  display: flex;
  align-items: center;
  gap: 2px;
  white-space: nowrap;
}

.breadcrumbs__item a {
  transition: color 0.3s;
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}

.breadcrumbs__item a:hover {
  color: #2427e1;
}

.breadcrumbs__item--current {
  color: #878E9C;
  pointer-events: none;
}

.breadcrumbs__separator {
  color: #878E9C;
  display: flex;
  align-items: center;
}

.breadcrumbs__separator svg {
  width: 10px;
  height: 10px;
}

