.socials {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.socials__item {
  width: 53px;
  height: 53px;
  border-radius: 100%;
  color: #fff;
  border: 1px solid #3F3A39;
  background-color: #3F3A39;
  display: flex !important;
  align-items: center;
  justify-content: center;
  transition: all 0.3s linear;
}

.socials__item:hover {
  border-color: #2427e1;
  background-color: #2427e1;
}

.socials__item svg {
  width: 20px;
  height: 14px;
}

.price {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 10px;
  color: var(--c-text);
  line-height: 1.2em;
  letter-spacing: -0.02em;
}

.price__current {
  font-weight: 700;
  font-size: 24px;
}

.price__old {
  font-weight: 700;
  font-size: 20px;
  text-decoration: line-through;
  opacity: 0.3;
}

.price__credit {
  flex-basis: 100%;
  font-weight: 500;
  font-size: 18px;
}

.price__credit span {
  opacity: 0.3;
}

.price--content {
  justify-content: flex-start;
}

.price--small {
  gap: 5px 10px;
  line-height: 1em;
}

.price--small .price__current {
  font-weight: 600;
  font-size: 20px;
}

.price--small .price__old {
  font-weight: 500;
  font-size: 16px;
}

.price--small .price__credit {
  font-weight: 400;
  font-size: 14px;
