.s-socials {
    max-width: 55.556vw;
    margin-left: auto;
    margin-right: 20px;

    @include max(laptop) {
        max-width: none;
        margin-right: 0;
    }

    @include max(mobile) {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    &__items {
        margin-top: 30px;
        display: grid;
        grid-template-columns: repeat(4, minmax(189px, max(1fr)));
        gap: 15px;

        @include max(laptop-small) {
            grid-template-columns: repeat(4, minmax(0, 1fr));
        }

        @include max(tablet) {
            grid-template-columns: repeat(2, minmax(0, 1fr));
            gap: 10px;
        }

        @include max(mobile) {
            margin-top: 50px;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        gap: 30px;
        background-color: $c-white;
        padding: 20px;
        border-radius: $br-section;
        font-family: $ff-rooftop;
        font-weight: 500;
        font-size: 15px;
        line-height: 120%;
        letter-spacing: 0;
        color: $c-text;
        cursor: pointer;

        @include max(mobile) {
            border-radius: $br-section-mob;
        }

        &--bg {
            background-color: $c-bg-main;
        }

        svg {
            width: 40px;
            height: 40px;
        }

        span {
            margin-top: auto;
        }
    }
}