h1,
h2,
h3,
h4,
h5,
h6,
ul {
	margin: 0;
	padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	text-wrap: balance;
}

input,
button,
textarea,
select {
	font-size: inherit;
	font-family: inherit;
	margin: 0;
	padding: 0;
	border: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

div,
input,
textarea,
select,
button,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span {
	outline: none;
	font-size: inherit;
	background: transparent;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

a,
a:hover {
	text-decoration: none;
}

img,
picture {
	display: block;
	max-width: 100%;
}

textarea {
	resize: none;
}

a {
	color: inherit;
}

ul,
ol {
	list-style: none;
}

table {
	border-spacing: 0;
}

body {
	display: flex;
	flex-direction: column;
	margin: 0;
	min-height: 100%;
	-webkit-font-smoothing: antialiased;
	-moz-font-smoothing: antialiased;
	-o-font-smoothing: antialiased;
	-webkit-locale: auto;
	white-space: normal;
	background-color: $c-bg-main;
	font-family: $ff-inter;
	font-size: 16px;
	font-weight: 400;
	line-height: 1.4em;
}

body.no-scroll {
	overflow: hidden;
}


html {
	scroll-behavior: smooth;
	-moz-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
	-o-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	text-size-adjust: 100%;
	scroll-padding-top: 90px;
}

html,
body {
	height: 100%;
}

html.hystmodal__opened {
	position: relative;
	top: 0 !important;
}

*:focus {
	outline: none;
}

.site-main {
	width: 100%;
}

.page-layout {
	position: relative;
	padding: 198px 0 0;

	@media print {
		background-color: #fff;
	}

	@include max(laptop) {
		padding: 198px 0 0;
	}

	@include max(tablet) {
		padding: 116px 0 0;
	}

	@include max(mobile) {
		padding: 70px 0 0;
	}

}

.section {
	padding: 0 30px 50px;

	@include max(large) {
		padding: 0 20px 50px;
	}

	@include max(tablet) {
		padding: 0 20px 40px;
	}

	@include max(mobile) {
		padding: 0 0px 30px;
	}

	&--title {
		padding: 0 30px 30px;

		@include max(large) {
			padding: 0 20px 30px;
		}

		@include max(tablet) {
			padding: 0 20px 20px;
		}

		@include max(mobile) {
			padding: 0;
		}
	}

	&__container {
		width: 100%;
		margin: 0 auto;
	}

	&__container--sidebar {
		display: flex;
		align-items: stretch;
		gap: 20px;

		@include max(tablet) {
			flex-direction: column;
		}
	}

	&__body {
		flex: 0 1 100%;
	}

	&__sidebar {
		flex: 0 0 453px;

		@include max(laptop-small) {
			flex-basis: 400px;
		}
	}

	&__header {
		margin-bottom: 20px;
	}

	&__content {
		@include max(mobile) {
			padding: 0 10px;
		}
	}

}

.section-block {
	position: relative;
	background-color: $c-white;
	border-radius: $br-section;
	padding: 20px;
	overflow: hidden;

	@include max(mobile) {
		border-radius: $br-section-mob;
		padding: 20px 10px;
	}

	&--dark {
		background-color: $c-bg-dark;
		color: $c-white;
	}

	&__bg {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;

		picture,
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__content {
		position: relative;
	}
}

.section-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 32px;
	margin-bottom: 72px;

	@include max(tablet) {
		margin-bottom: 40px;
		gap: 24px;
	}

	@include max(mobile) {
		gap: 12px;
	}

	&__title {
		flex: 0 1 auto;
		padding-left: 10px;
	}

	&__btn.btn {
		flex: 0 0 auto;

		@include max(mobile) {
			padding: 15.5px 10px;
			font-size: 12px;
		}
	}
}

.title {
	color: $c-text;
	letter-spacing: 0;
	line-height: 100%;

	&--light {
		color: $c-white;
	}

	span {
		color: $c-accent-2;
		display: block;
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		color: inherit;
		line-height: inherit;
		font-size: inherit;
		letter-spacing: inherit;
		font-weight: inherit;
		text-wrap: inherit;
	}

	&--50 {
		font-size: 50px;
		line-height: 1em;
		font-weight: 800;
		font-family: $ff-rooftop;

		@include max(laptop) {
			font-size: 40px;
		}

		@include max(mobile) {
			font-size: 32px;
		}

		@include max(xs) {
			font-size: 21px;
		}
	}

	&--40 {
		font-family: $ff-rooftop;
		font-size: 40px;
		font-weight: 500;
		line-height: 1em;

		@include max(laptop) {
			font-size: 32px;
		}

		@include max(mobile) {
			font-size: 24px;
		}
	}

	&--36 {
		font-family: $ff-rooftop;
		font-size: 36px;
		font-weight: 500;
		line-height: 1.2em;

		@include max(laptop) {
			font-size: 28px;
		}

		@include max(mobile) {
			font-size: 22px;
		}
	}

	&--30 {
		font-family: $ff-rooftop;
		font-size: 30px;
		font-weight: 500;
		line-height: 1.2em;
		letter-spacing: -0.02em;

		@include max(laptop) {
			font-size: 24px;
		}

		@include max(mobile) {
			font-size: 20px;
		}
	}

	&--24 {
		font-family: $ff-inter;
		font-size: 24px;
		font-weight: 700;
		line-height: 1.2em;
		letter-spacing: -0.02em;

		@include max(laptop) {
			font-size: 20px;
		}

		@include max(mobile) {
			font-size: 18px;
		}
	}

	&--18 {
		font-family: $ff-inter;
		font-size: 18px;
		font-weight: 600;
		line-height: 1em;
		letter-spacing: 0;

		@include max(mobile) {
			font-size: 16px;
		}
	}

}

.title-sticker {
	font-family: $ff-inter;
	padding: 20px 25px;
	color: $c-white;
	background-color: $c-accent;
	border-radius: 25px;
	font-weight: 800;
	font-size: 16px;
	line-height: 1.2em;
	letter-spacing: 0;

	@include max(tablet) {
		border-radius: 10px;
	}

	@include max(mobile) {
		background-color: $c-accent-2;
	}

	span {
		display: block;
		padding-top: 6px;
		font-family: $ff-rooftop;
		font-weight: 800;
		font-size: 30px;
		line-height: 1em;
		letter-spacing: 0;

		@include max(tablet) {
			font-size: 24px;
		}
	}

}

.bullet-progress.swiper-pagination-bullets {
	@include max(mobile) {
		display: flex;
		justify-content: center;
	}

	.swiper-pagination-bullet {
		background-color: rgba(255, 255, 255, 0.4);
		width: 30px;
		height: 10px;
		border-radius: 12px;
		position: relative;
		overflow: hidden;
		opacity: 1;
		transition: width 0.1s linear;
	}

	.bullet-progress__overlay {
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 0%;
		background-color: $c-white;
	}

	.swiper-pagination-bullet-active {
		width: 354px;
		background-color: rgba(255, 255, 255, 0.4);
		opacity: 1;

		@include max(mobile) {
			width: 110px;
		}
	}

	.swiper-pagination-bullet .bullet-progress__overlay {
		display: none;
	}

	.swiper-pagination-bullet-active .bullet-progress__overlay {
		display: block;
	}
}

.text-editor {
	color: $c-text-editor;
	font-weight: 400;
	font-size: 16px;
	line-height: 1.6em;
	letter-spacing: 0;

	@include max(mobile) {
		font-size: 15px;
		line-height: 1.5em;
	}

	*:first-child {
		margin-top: 0 !important;
	}

	*:last-child {
		margin-bottom: 0 !important;
	}

	h2,
	h3 {
		font-family: $ff-rooftop;
		font-weight: 500;
		font-size: 22px;
		line-height: 1.3em;
		letter-spacing: 0;
		margin-bottom: 0.9em;

		@include max(mobile) {
			font-size: 20px;
			line-height: 1.2em;
			margin-bottom: 1em;

		}
	}

	p,
	ul,
	ol,
	a,
	li {
		font-size: inherit;
	}

	p,
	ul,
	ol {
		margin-bottom: 1.25em;
	}

	ol,
	ul {
		display: flex;
		flex-direction: column;
		gap: 0.9375em;
		list-style-type: none;

		@include max(mobile) {
			gap: 0.667em;
		}
	}

	ol {
		counter-reset: num;
	}

	ol li {
		position: relative;
		padding-left: 1.5em;
		line-height: 1.4em;

		&:before {
			content: counter(num) '.';
			counter-increment: num;
			position: absolute;
			left: auto;
			right: calc(100% - 1em);
			font-weight: 500;
		}
	}

	ul li {
		position: relative;
		padding-left: 2.5625em;
		line-height: 1.4375em;

		@include max(mobile) {
			padding-left: 2.07em;
			line-height: 1.5em;
		}

		&:before {
			content: '';
			position: absolute;
			left: 1.25em;
			top: 0.71875em;
			width: 0.5em;
			height: 0.5em;
			transform: translateY(-50%);
			border-radius: 100%;
			background-color: $c-accent-2;

			@include max(mobile) {
				left: 0.667em;
				top: 0.467em;
				width: 0.533em;
				height: 0.533em;
				transform: none;
			}
		}
	}

	a {
		color: $c-accent;
		transition: all $tr-time linear;

		&:hover {
			color: $c-accent-hover;
		}
	}

	img,
	video,
	iframe,
	.video {
		max-width: 100%;
		margin: 1.875em auto;
		border-radius: $br-section;

		@include max(mobile) {
			border-radius: $br-section-mob;
		}
	}

	img[align="left"] {
		width: 100%;
		object-fit: contain;
		object-position: left;
	}

	img[align="right"] {
		width: 100%;
		object-fit: contain;
		object-position: right;
	}

