.scrollbar {
	&::-webkit-scrollbar {
		width: 6px;
		height: 6px;
	}

	&::-webkit-scrollbar-track {
		background-color: $c-text-light;
		border-radius: 2px;
	}

	&::-webkit-scrollbar-thumb {
		background-color: $c-bg-main;
		border-radius: 2px;
	}

	&--small {
		&::-webkit-scrollbar {
			width: 3px;
			height: 3px;
		}
	}
}

.my-fancybox {
	.fancybox__viewport {
		.fancybox__slide {
			padding-left: 40px;
			padding-right: 40px;

			@include max(laptop) {
				padding-left: 20px;
				padding-right: 20px;
			}

			@include max(mobile) {
				padding-left: 15px;
				padding-right: 15px;
			}
		}

		.fancybox__content {
			border-radius: 4px;
			overflow: hidden;
			padding: 0;

			@include max(2000) {
				max-width: 1200px;
				max-height: 675px;
			}

			@include max(laptop) {
				max-width: 840px;
				max-height: 472.5px;
			}

			@include max(tablet) {
				max-width: 95vw;
				max-height: none;
			}

			@include max(mobile) {
				max-width: 335px;
			}
		}

		.fancybox__html5video,
		.fancybox__iframe {
			aspect-ratio: 16 / 9;
		}

	}

	.f-thumbs {
		@include max(mobile) {
			padding: 0 20px 8px;
		}

		.f-thumbs__slide {
			width: 100px;
			height: 67px;
			margin: 0 10px 0 0;

			@include max(laptop) {
				width: 100px;
				height: 67px;
				margin: 0 10px 0 0;
			}

			@include max(mobile) {
				width: 80px;
				height: 54px;
				margin: 0 5px 0 0;
			}

			&__button {
				width: 100%;
				opacity: 1;

				&:after {
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					height: auto;
					bottom: 0;
					border: 2px solid #585A60;
					border-radius: 2px;
					animation: f-fadeIn 0.2s ease-out;
					z-index: 10;
				}
			}

			&__img {
				border-radius: 2px;
				object-fit: cover;
			}

		}

		.f-thumbs__slide.is-nav-selected {
			.f-thumbs__slide__button {
				&:after {
					border: 2px solid $c-accent-hover;
					border-radius: 2px;
				}
			}
		}
	}

	.fancybox__toolbar {
		.f-button[data-fancybox-close] {
			border-radius: 16px;
			margin-top: 40px;
			margin-right: 40px;
			width: 56px;
			height: 56px;

			&:hover {
				background-color: $c-accent-hover;
			}

			@include max(laptop) {
				margin-top: 30px;
				margin-right: 30px;
				width: 46px;
				height: 46px;
			}

			@include max(mobile) {
				margin-top: 20px;
				margin-right: 20px;
				width: 32px;
				height: 32px;
				border-radius: 8px;
			}

			svg {
				width: 24px;
				height: 24px;
				color: white;

				@include max(mobile) {
					width: 16px;
					height: 16px;
				}
			}
		}
	}

	.fancybox__nav {
		.f-button {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 59px;
			height: 59px;
			background-color: rgba(255, 255, 255, 0.7);
			backdrop-filter: blur(40px);
			border-radius: 16px;
			color: $c-accent;
			transition: all $tr-time linear;
			cursor: pointer;

			@include max(mobile) {
				width: 32px;
				height: 32px;
				border-radius: 8px;
			}

			svg {
				width: 24px;
				height: 24px;
				box-shadow: none;
				filter: none;

				@include max(mobile) {
					width: 16px;
					height: 16px;
				}
			}

			&:hover {
				border-color: $c-accent-hover;
				color: $c-white;
				background-color: $c-accent-hover;
			}

		}

		.f-button.is-prev {
			left: 40px;

			@include max(laptop) {
				left: 30px;
			}

			@include max(mobile) {
				left: 5px;
			}
		}

		.f-button.is-next {
			right: 40px;

			@include max(laptop) {
				right: 30px;
			}

			@include max(mobile) {
				right: 5px;
			}

		}
	}
}

